import React, { Component } from "react";
import Dustbin from "./examples/dustbin/index.js";
import ChessBoard from "./examples/chess/index.js";

import "./App.css";
class App extends Component {
  render() {
    return (
      <div className="App">
        Paratrooper Examples
        <Dustbin />
        <div style={{ marginTop: 20 }}>
          <ChessBoard />
        </div>
      </div>
    );
  }
}

export default App;



